  const code6 = `  // Cdigo con la lgica del Spawner
  
using System.Collections;
using System.Collections.Generic;
using UnityEngine;

public class Spawner : MonoBehaviour
{
    public List<GameObject> objsToSpawn; // Lista de prefabs de objetos a instanciar
    public Vector3 offset; // Desplazamiento para determinar el rea de instanciacin (rea donde pueden aparecer)
    public LayerMask obstacleMask; // Layers sobre los que no puede instanciarse un objeto
    public float timeToSpawn; // Cada cuanto tiempo se instancia un item

    void Start()
    {
        InvokeRepeating("Spawn", timeToSpawn, timeToSpawn); // Llamo al mtodo 'Spawn' cada cierto tiempo con Invoke Repeating
    }

    private void Spawn() // Mtodo para spawnear un objeto
    {
        GameObject obj = Instantiate(GetObj(), GetPosition(), Quaternion.identity); // Instancia el objeto (obtenido por el mtodo), en una posicin (obtenida por el mtodo), sin rotacin
        obj.transform.SetParent(this.transform); // El objeto instanciado se hace hijo del instanciador en la jerarqua --> del objeto vaco "spawner"
    }

    private GameObject GetObj() // Mtodo para obtener un prefab aleatorio de la lista de prefabs
    {
        return objsToSpawn[Random.Range(0, objsToSpawn.Count)]; // Devuelve un objeto de la lista dado un ndice aleatorio en un rango
    }

    private Vector3 GetPosition() // Mtodo para obtener una posicion aleatoria en un rea con unos lmites
    {
        // Devuelve un Vector3 (posicion) a partir del punto en el que est el instanciador al que se le suman unos valores aleatorios de desplazamientos
        Vector3 pos = transform.position + new Vector3(Random.Range(-offset.x, offset.x), Random.Range(-offset.y, offset.y), Random.Range(-offset.z, offset.z));// Calculo posicin en el rea
        int timeOut = 100; // 100 intentos para buscar una posicin libre
        while(CheckAvailablePosition(pos) == false && timeOut > 0) // Si no es buena posicin o se ha agotado el nmero de repeticiones
        {
            pos = transform.position + new Vector3(Random.Range(-offset.x, offset.x), Random.Range(-offset.y, offset.y), Random.Range(-offset.z, offset.z)); // Calculo nueva posicin en el rea
            timeOut--; // Reducimos el contador de repeticiones
        }
        return pos; // Devuelvo la posicin
    }

    private bool CheckAvailablePosition(Vector3 pos) // Mtodo que determina si una posicin es buena para instanciar
    {
        return Physics.OverlapSphere(pos, 1f, obstacleMask).Length == 0; // Si el nmero de objetos con el que choca al dibujar una esfera es cero significa que no hay obstaculos para instanciar
    }
}`;


